export const getPixelSize = (size: "small" | "medium" | undefined) => {
  return size === "small" ? "32px" : "40px"
}

export function numberLoop(max: number, actual: number, add: number) {
  const sum = actual + add
  if (sum === 0) return max
  if (max % sum === max) return sum - max
  return sum
}
